@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@font-face {
  font-family: "Sega";
  src: url(./fonts/SEGA.woff);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  text-align: center;
  background-image: linear-gradient(to right, #01747b, #062356);
}

.container {
  overflow: auto;
  padding: 30px;
}

.loader {
  /* margin: 0 auto;
  /* border: 20px solid black; 
  border-left: 20px solid indianred;
  border-right: 20px solid indigo;
  border-bottom: 20px solid white;
  border-top: 20px solid white;
  border-radius: 100%;
  width: 200px;
  height: 200px;
  margin-top: 30vh;
  animation: loading 1s linear infinite; */

  color: #ffffff;
  font-size: 20px;
  margin: 30vh auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: loading 1s infinite linear;
  animation: loading 1s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}

h1 {
  font-family: "Sega";
  text-align: center;
  font-size: 3.5em;
  font-weight: 700;
  margin-bottom: 20px;
  color: #0ccac4;
}
input {
  height: 55px;
  width: 35vw;
  margin-left: 30vw;
  margin-right: 30vw;
  margin-bottom: 20px;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  background: #9eebcf;
  color: #000;
  width: 300px;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.card:hover {
  transform: scale(1.05);
}

img {
  width: 200px;
  margin-bottom: 10px;
  object-fit: contain;
}

.card h3 {
  font-size: 25px;
  font-weight: 900;
  text-align: center;
}

.card h4 {
  text-align: center;
}

.robot-detail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.robot-detail div {
  width: 100%;
  height: 2px;
  background-color: white;
  margin-bottom: 10px;
}

.robot-detail span {
  font-size: 10px;
  font-weight: 900;
}
.robot-detail h4 {
  font-size: 15px;
}

.robot-detail .inner {
  display: flex;
  align-self: start;
}

